import './App.css';
import Footer from "./components/Footer";
import {BrowserRouter as Router ,Switch, Route} from "react-router-dom";
import Header from "./components/Header";
import Home from "./components/Home";
import Products from "./components/Products";

function App() {
  return (
    <Router>
    <div className="container d-flex flex-column justify-content-between center-one ">
     <header>
       <Header/>
     </header>
      <section className="main">
          <Switch>
            <Route path="/products">
              <Products/>
            </Route>
            <Route path="/">
              <Home/>
            </Route>
          </Switch>
      </section>
      <footer>
        <Footer></Footer>
      </footer>
    </div>
    </Router>
  );
}

export default App;
